@import '../variables', 'utils/variables', 'utils/mixins';

.list {
  width: 1082px;
  max-width: 100%;

  padding: 0 24px 16px 24px;

  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.item {
  width: 100%;

  color: $base-black;

  border: 1px solid $light-gray;
  border-radius: 2px;

  margin-bottom: 4px;
}

.separateLine {
  border-bottom: 1px solid $base-gray;
  width: 100%;
}
