@import '../variables', 'utils/variables', 'utils/mixins';

.wrapper {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;

  padding: 12px 16px;

  transition: background-color 0.2s ease-in-out;
  will-change: background-color;

  &:hover {
    background-color: $light-gray;
  }

  @include darkTheme {
    color: $moderate-blue;

    &:hover {
      background: $black-grey-light;
    }
  }
}

.name {
  width: 100%;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  margin-right: 24px;
}

.updated {
  min-width: 156px;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  margin-right: 24px;
}

.status {
  width: $status-width;
}

.checkNumber {
  width: $check-number-width;
}

.actions {
  display: flex;
  flex-direction: row;
  align-items: center;

  margin-left: 20px;
  margin-right: 8px;
}

.action {
  width: 15px;
  height: 15px;

  cursor: pointer;

  margin-right: 10px;

  &:not(:first-child) {
    margin-left: 10px;
  }

  @include darkTheme {
    path {
      fill: $moderate-blue;
    }
  }
}

.actionDisabled {
  opacity: 0.55;
  cursor: default;
}

.editContainer {
  padding: 4px 10px;
  margin-right: 96px;

  height: 32px;
  width: 100%;

  background-color: $white;
  border: 0.5px solid #064fa3;
  box-sizing: border-box;
  border-radius: 2px;

  font-size: 10px;
  line-height: 17px;
  color: #2a2f42;

  display: flex;
  align-items: center;
  justify-content: space-between;

  @include darkTheme {
    color: $white;
    background: $black-blue;
    border-color: $grey;
  }
}

.input {
  border: none;
  outline: none;

  height: 100%;

  @include darkTheme {
    color: $white;
    background: $black-blue;
  }
}

.close {
  cursor: pointer;
  width: 9px;
  height: 9px;
}

.button {
  width: auto;
  height: 40px;
  padding: 8px 24px;

  font-size: 16px;
  line-height: 24px;
  color: $base-black;

  background-color: $white;
  border: 1px solid $dark-blue;
  border-radius: 2px;

  white-space: nowrap;
}

.openSchemeIcon {
  width: 19px;
  height: 19px;

  margin-right: 10px;
  margin-left: -8px;
}

.contextMenuIcon {
  width: 18px;
  height: 18px;

  cursor: pointer;
}

.contextMenu {
  width: 200px;
  height: 81px;

  background-color: $white;
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.15);

  position: absolute;
  right: 55px;

  display: flex;
  flex-direction: column;
}

.contextMenuItem {
  cursor: pointer;

  height: 40px;
  padding: 12px 16px;

  display: flex;
  flex-direction: row;
  align-items: center;

  &:first-child {
    border-bottom: 1px solid $base-gray;
  }

  &:hover {
    background-color: $base-gray;
  }
}
