@import './variables', 'utils/variables', 'utils/mixins';

.search {
  height: 40px;
  width: 360px;
}

.headerFixed {
  top: 0;
  background-color: white;

  @include darkTheme {
    background: $black-blue;
  }
}

.headerSearch {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 16px 24px;
}

.paginationWrapper {
  display: flex;
  align-items: center;
  justify-content: stretch;
}

.pagination {
  width: 100%;
}

.total {
  font-size: 14px;
  line-height: 18px;

  white-space: nowrap;

  margin-right: 12px;

  @include darkTheme {
    color: $white;
  }
}
